import React, { Suspense, lazy, useState, useEffect } from "react";
import { Switch, Route, withRouter } from "react-router-dom";
import { Popover, OverlayTrigger, Overlay } from 'react-bootstrap';
import AllTime from './all-time';
import Individual from './individual';
import Weekly from './weekly';
import {TransitionGroup, CSSTransition} from "react-transition-group";
import '../css/style.css';
import "bootstrap/dist/css/bootstrap.min.css";
    

var Container = ({location}) => {


    return (     
      <>             
        <div class="front">
            <TransitionGroup className="transition-group" >
                <CSSTransition key={location.key} timeout={{enter: 150, exit: 0}} classNames={'fade'}>
                    <section className="route-section">
                        <Switch key={location.key} location={location}>                
                            <Route path="/all-time" component={AllTime} />
                            <Route path="/individual" component={Individual} />
                            <Route path="/weekly" component={Weekly} />
                            <Route path="/">
                                <AllTime />           
                            </Route>                            
                        </Switch>
                    </section>
                    </CSSTransition>
            </TransitionGroup>
            </div>     
        </>
    );
}


export default withRouter(Container);