import React, { useState, useEffect } from 'react';
import '../css/style.css';
import mvp from '../assets/Scoreboard_mvp.webp'
import imgPlanted from '../assets/bomb_planted.png'
import imgDefused from '../assets/defusal_kit.webp'
import imgExploded from '../assets/c4_explosion_icon.png'
import icecream from '../assets/ice-cream.png'
import { Button, Card, CardDeck, Container, Modal, Col, Row, Form, Jumbotron } from 'react-bootstrap';
import { BarChart, Brush, ReferenceLine, Cell, RadarChart, PieChart, Pie, ResponsiveContainer, Label, Treemap, RadialBarChart, RadialBar, PolarGrid, PolarAngleAxis, PolarRadiusAxis, Radar, Bar, YAxis, Legend, Text, LineChart, XAxis, Tooltip, CartesianGrid, Line } from 'recharts'
const config = require('../config.json');

const steamIDs = require('../data/steamIDs.json')


var Individual = () => {

  const [selectedPlayer, setSelectedPlayer] = useState("STEAM_1:0:55622278");  


  const [kentoData, setKentoData] = useState([{}]);  
  const [aioData, setAioData] = useState([{}]);  

  var player = {
    steam: selectedPlayer
  }

  useEffect(() => {
    fetch("https://safe-thicket-77515.herokuapp.com/api/rankme-ind", {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify(player)
})
    .then(res => res.json())
    .then (
      (result) => {
        setKentoData(result);
      },
      (error) =>{
        setKentoData(error);
      }
    )
  }, [])


  useEffect(() => {
    fetch("https://safe-thicket-77515.herokuapp.com/api/rankme-aio", {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify(player)
})
    .then(res => res.json())
    .then (
      (result) => {
        setAioData(result);
      },
      (error) =>{
        setAioData(error);
      }
    )
  }, [])

  const changePlayer = (newPlayer) => {

    var playerTo = {
      steam: newPlayer
    }

    fetch("https://safe-thicket-77515.herokuapp.com/api/rankme-ind", {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify(playerTo)
})
    .then(res => res.json())
    .then (
      (result) => {
        setKentoData(result);
      },
      (error) =>{
        setKentoData(error);
      }
    );

    fetch("https://safe-thicket-77515.herokuapp.com/api/rankme-aio", {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify(playerTo)
})
    .then(res => res.json())
    .then (
      (result) => {
        setAioData(result);
      },
      (error) =>{
        setAioData(error);
      }
    );
  }




  // add new kv pairs to json
  Object.keys(kentoData).map(
    function(object){
      kentoData[object]["accuracy"]= +kentoData[object].hits / +kentoData[object].shots * 100
  });

  Object.keys(kentoData).map(
    function(object){
      kentoData[object]["kd"]= +kentoData[object].kills / +kentoData[object].deaths
  });

  Object.keys(kentoData).map(
    function(object){
      kentoData[object]["kda"]= (+kentoData[object].kills +  +kentoData[object].assists) / +kentoData[object].deaths
  });

  Object.keys(kentoData).map(
    function(object){
      kentoData[object]["sumOtherHits"]= +kentoData[object].left_arm + +kentoData[object].left_leg + +kentoData[object].right_arm + +kentoData[object].right_leg
  });

  // add new kv pairs to json
  Object.keys(aioData).map(
    function(object){
      aioData[object]["accuracy"]= +aioData[object].hits / +aioData[object].shots * 100
  });

  Object.keys(aioData).map(
    function(object){
      aioData[object]["kd"]= +aioData[object].kills / +aioData[object].deaths
  });

  Object.keys(aioData).map(
    function(object){
      aioData[object]["kda"]= (+aioData[object].kills +  +aioData[object].assists) / +aioData[object].deaths
  });

  Object.keys(aioData).map(
    function(object){
      aioData[object]["sumOtherHits"]= +aioData[object].left_arm + +aioData[object].left_leg + +aioData[object].right_arm + +aioData[object].right_leg
  });



var sortAllWeapons = (a, b) => {
  return b.kills-a.kills
};

const extraCalc = () => {
  var data = aioData.filter((data) => { return data.kills != 0 })

  Object.keys(data).map(
    function(object){
      data[object]["accuracy"]= +data[object].hits / +data[object].shots * 100;
      data[object]["kd"]= +data[object].kills / +data[object].deaths
  });

  return data
}


    return (
      <>
 
 <Container>    
<br/><br/><br/>
<Row>
  <Col>
  <Form.Group controlId="exampleForm.ControlSelect2">
    <Form.Label>Select player</Form.Label>
    <Form.Control as="select" onChange={(e) => {changePlayer(e.target.value)}}>
    {steamIDs    
    .sort((a, b) => a.name.localeCompare(b.name))
    .map((player, key) => {
      return <option value={player.steam}>{player.name}</option>
    })}
    </Form.Control>
  </Form.Group>
  </Col>
  <Col></Col>
  <Col></Col>
  </Row>



{kentoData
/*.filter((player) => {
  return player.steam === selectedPlayer
})*/
.map(function(player, key){
  
  const COLORS = ['#C7007A', '#34ebb1'];
 

  function timeConvert(duration)
  {   
      // Hours, minutes and seconds
      var hrs = ~~(duration / 3600);
      var mins = ~~((duration % 3600) / 60);
      var secs = ~~duration % 60;
  
      // Output like "1:01" or "4:03:59" or "123:03:59"
      var ret = "";
  
      if (hrs > 0) {
          ret += "" + hrs + ":" + (mins < 10 ? "0" : "");
      }
  
      ret += "" + mins + ":" + (secs < 10 ? "0" : "");
      ret += "" + secs;
      return ret;
  }

var playerKda = [
  {kills: player.kills, deaths: player.deaths, assists: player.assists}
]


var winData = [
    {name: "ct wins", value: player.ct_win},
    {name: "tr wins", value: player.tr_win}
  ]
  
var allWeapons = [
  { weapon: 'Knife', kills: player.knife },
  { weapon: 'Glock', kills: player.glock },
  { weapon: 'usp_silencer', kills: player.usp_silencer },
  { weapon: 'P250', kills: player.p250 },
  { weapon: 'deagle', kills: player.deagle },
  { weapon: 'elite', kills: player.elite },
  { weapon: 'hkp2000', kills: player.hkp2000 },
  { weapon: 'fiveseven', kills: player.fiveseven },
  { weapon: 'tec9', kills: player.tec9 },
  { weapon: 'cz75a', kills: player.cz75a },
  { weapon: 'revolver', kills: player.revolver },
  { weapon: 'nova', kills: player.nova },
  { weapon: 'xm1014', kills: player.xm1014 },
  { weapon: 'mag7', kills: player.mag7 },
  { weapon: 'sawedoff', kills: player.sawedoff },
  { weapon: 'm249', kills: player.m249 },  
  { weapon: 'negev', kills: player.negev },   
  { weapon: 'mac10', kills: player.mac10 },
  { weapon: 'mp9', kills: player.mp9 },
  { weapon: 'mp7', kills: player.mp7 },
  { weapon: 'ump45', kills: player.ump45 },
  { weapon: 'p90', kills: player.p90 },    
  { weapon: 'bizon', kills: player.bizon },  
  { weapon: 'galilar', kills: player.galilar },
  { weapon: 'ak47', kills: player.ak47 },
  { weapon: 'scar20', kills: player.scar20 },
  { weapon: 'famas', kills: player.ump45 },
  { weapon: 'm4a1', kills: player.m4a1 },    
  { weapon: 'm4a1_silencer', kills: player.m4a1_silencer },  
  { weapon: 'aug', kills: player.aug },  
  { weapon: 'ssg08', kills: player.ssg08 },  
  { weapon: 'awp', kills: player.awp },  
  { weapon: 'g3sg1', kills: player.g3sg1 }, 
  { weapon: 'SG 553', kills: player.sg556 },    
  { weapon: 'taser', kills: player.taser },      
];



  return(
    <>

    <Jumbotron>
  <Container>
    <Row>
      <Col><h1>{player.name}</h1></Col>    
    </Row>
    <Container>
    <Row>
     {player.name === "watkinsaj" && <p><img src={icecream} width="31px" height="31px" />2 &nbsp;</p>  }
      <p><img src={mvp}/> {player.mvp} &nbsp;</p>
      <p><img src={imgPlanted} height="31px" width="31px" /> {player.c4_planted} &nbsp;</p>
      <p><img src={imgExploded} height="31px" width="31px" /> {player.c4_exploded} &nbsp;</p>
      <p><img src={imgDefused} height="30px" width="30px" /> {player.c4_defused} &nbsp;</p>
    </Row>   
    </Container>  
    <Row>
      <Container>
     <p>{timeConvert(player.connected)}</p>
      </Container>
    </Row>
  </Container>
</Jumbotron>
<Jumbotron fluid>
<Row>
<Col xs={9}>
    <ResponsiveContainer width={900} height={350}>
    <LineChart width={730} height={250} data={aioData.filter((data) => { return data.kills != 0 })}
      margin={{ top: 5, right: 30, left: 20, bottom: 5 }}>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="date" />
        <YAxis />
        <Tooltip />
        <Legend />
     {/*   <ReferenceLine y={0} stroke="#000" />
        <Brush dataKey="date" height={30} stroke="#8884d8" /> */}
        <Line type="monotone" dataKey="kills" stroke="#8884d8" />
        <Line type="monotone" dataKey="deaths" stroke="#34ebb1" />
        <Line type="monotone" dataKey="headshots" stroke="#e8eb34" />
        <Line type="monotone" data={extraCalc()} dataKey="accuracy" stroke="#C7007A" />
        {/* <Line type="monotone" data={kdCalc()} dataKey="kd" stroke="#8884d8" /> */}
      </LineChart>
      </ResponsiveContainer>
      <br/>

      <ResponsiveContainer width={900} height={350}>
      <LineChart data={extraCalc()}
      margin={{ top: 5, right: 30, left: 20, bottom: 5 }}>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="date" />
        <YAxis />
        <Tooltip />
        <Legend />
        <Line type="monotone" dataKey="kd" stroke="#a805ff" />
      </LineChart>
      </ResponsiveContainer>
<Row>

   
<ResponsiveContainer width={250} height={250}>
      <BarChart     
        data={playerKda}
        margin={{ top: 5, right: 20, left: 10, bottom: 5 }}      >

        <XAxis dataKey="name" />
        <YAxis />
        <Tooltip />        
        <Bar dataKey="kills" fill="#C7007A" />
        <Bar dataKey="deaths" fill="#34ebb1" />
        <Bar dataKey="assists" fill="#e8eb34" />
        <Legend />
        </BarChart>
</ResponsiveContainer>


<ResponsiveContainer width={250} height={250}>
<PieChart width={250} height={250}>
      <Legend verticalAlign="bottom" height={-10}/>
        <Pie dataKey="value" isAnimationActive={false} data={winData} cx={125} cy={100} outerRadius={80} fill="#8884d8" label >
        {
            winData.map((entry, index) => <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />)
          }
          </Pie>          
        <Tooltip />        
      </PieChart>
  </ResponsiveContainer>

  <ResponsiveContainer height={300}>
      <BarChart data={allWeapons.sort(sortAllWeapons)} 
            margin={{top: 20, right: 30, left: 20, bottom: 5}}>
       <CartesianGrid strokeDasharray="3 3"/>
       <XAxis dataKey="weapon" tick={{fontSize: "10px", textAnchor: "end", angle: "-30"}} interval={0}/>
       <YAxis  domain={[0, 60]}/>
       <Tooltip cursor={{ fill: 'rgba(206, 206, 206, 0.4)' }}/>
       <Legend wrapperStyle={{bottom: 0, left: 25}} />
       <Bar dataKey="kills" fill="#4c32a8" name="kills by weapon" /> 
     
      </BarChart>
      </ResponsiveContainer>
  


 </Row>
  </Col>
 </Row>
 </Jumbotron>
</>

  )
})}



</Container>  
     </>
    );
  }
  
export default Individual;